<template>
  <list-page :title="$t('settings.account.title')">
    <cell-panel
      :placeholder="$t('settings.account.email.title')"
      :title="$t('settings.account.iemail')"
      :value="email"
      :disable="loginByEmail"
      @click="toAddEmail"
    />
    <cell-panel
      :placeholder="$t('settings.account.phone.title')"
      :title="$t('settings.account.iphone')"
      :value="phone"
      @click="toAddPhone"
    />
    <cell
      v-if="loginByEmail"
      :title="$t('settings.account.pwd.title')"
      arrow
      @click="toChangePassword"
    />
    <cell
      :title="$t('settings.account.signOut')"
      @click="signOut"
    />
  </list-page>
</template>

<script>
import { mapState } from 'vuex'
import ListPage from '@/components/list/ListPage.vue'
import Cell from '@/components/list/Cell.vue'
import CellPanel from '@/components/list/CellPanel.vue'

export default {
  name: 'SettingsAccount',
  components: {
    ListPage,
    Cell,
    CellPanel
  },
  computed: {
    ...mapState(['authInfo']),
    loginByEmail () {
      return this.authInfo && this.authInfo.type === 3
    },
    email () {
      return 'abc@qq.com'
    },
    phone () {
      return '+861368745874'
    }
  },
  methods: {
    toAddEmail () {
      this.$router.push({
        name: 'add-email'
      })
    },
    toAddPhone () {
      this.$router.push({
        name: 'add-phone'
      })
    },
    toChangePassword () {
      this.$router.push({
        name: 'change-password'
      })
    },
    signOut () {
      this.$toast(this.$t('settings.account.signOut'))
    }
  }
}
</script>

<style lang="scss" scoped>
</style>
