<template>
  <list-page
    :title="$t('sign.signup.title')"
    :button="$t('sign.signup.btn')"
    :disable="disable"
    short
    @click="toSignUp"
  >
    <div class="c-vlist-item--nearer l-flex--row is-top">
      <field
        class="l-flex__fill"
        v-model="firstName"
        :title="$t('sign.signup.firstName')"
        :placeholder="$t('sign.signup.firstName')"
        :error="firstNameError"
        maxlength="12"
        @input="firstNameError = ''"
      />
      <field
        class="l-flex__fill u-h-separate"
        v-model="lastName"
        :title="$t('sign.signup.lastName')"
        :placeholder="$t('sign.signup.lastName')"
        :error="lastNameError"
        maxlength="12"
        @input="lastNameError = ''"
      />
    </div>
    <field
      class="c-vlist-item--nearer"
      v-model="email"
      :title="$t('sign.info.email')"
      :placeholder="$t('sign.info.email')"
      :error="emailError"
      @input="emailError = ''"
    />
    <field
      class="c-vlist-item--nearer"
      v-model="password"
      type="password"
      :title="$t('sign.info.password')"
      :placeholder="$t('sign.info.password')"
      :tip="$t('sign.signup.passwordTip')"
      maxlength="16"
      :error="passwordError"
      @input="passwordError = ''"
    />
    <field
      class="c-vlist-item--nearer"
      v-model="rpassword"
      type="password"
      :title="$t('sign.info.rpassword')"
      :placeholder="$t('sign.info.rpassword')"
      maxlength="16"
      :error="rpasswordError"
      @input="rpasswordError = ''"
    />
    <field
      class="c-vlist-item--nearer"
      :value="birthDay"
      :title="$t('sign.signup.birth')"
      :placeholder="$t('sign.signup.birthPlaceholder')"
      :error="birthError"
      readonly
      clickable
      @click="onClickBirth"
    />
    <field
      class="c-vlist-item--nearer"
      v-model="country2city"
      :title="$t('sign.signup.country')"
      :placeholder="$t('sign.signup.countryPlaceholder')"
      :error="countryError"
      readonly
      clickable
      @input="countryError = ''"
      @click="onClickCountry"
    />
    <van-checkbox
      class="c-vlist-item--nearer o-checkbox"
      v-model="checked"
      checked-color="#00A9E0"
    >
      {{ $t('sign.signup.agree') }}<span class="u-color--blue">{{ $t('sign.signup.agree1') }}</span>
    </van-checkbox>
    <van-checkbox
      class="c-vlist-item--nearer o-checkbox"
      v-model="checked2"
      checked-color="#00A9E0"
    >
      {{ $t('sign.signup.agree2') }}
    </van-checkbox>
    <van-popup
      v-if="showBirthPicker"
      v-model="showBirth"
      position="bottom"
      @closed="onHideBirth"
    >
      <van-datetime-picker
        type="date"
        :value="currentDate"
        :min-date="minDate"
        :max-date="maxDate"
        :confirm-button-text="$t('btn.done')"
        :cancel-button-text="$t('btn.cancel')"
        @confirm="onConfirmBirth"
        @cancel="showBirth = false"
      />
    </van-popup>
    <van-popup
      v-if="showCountryPicker"
      v-model="showCountry"
      position="bottom"
      @closed="onHideCountry"
    >
      <van-picker
        :columns="columns"
        :show-toolbar="true"
        :confirm-button-text="$t('btn.done')"
        :cancel-button-text="$t('btn.cancel')"
        @confirm="onConfirmCountry"
        @cancel="showCountry = false"
        @change="onChangeCountry"
      />
    </van-popup>
  </list-page>
</template>

<script>
import { mapState } from 'vuex'
import { checkName, checkEmail, checkPassword } from '@/utils/patt.js'
import { dateFormat } from '@/utils/format.js'
import { Popup, DatetimePicker, Picker, Checkbox } from 'vant'
import ListPage from '@/components/list/ListPage.vue'
import Field from '@/components/list/Field.vue'

export default {
  components: {
    [ Popup.name ]: Popup,
    [ DatetimePicker.name ]: DatetimePicker,
    [ Picker.name ]: Picker,
    [ Checkbox.name ]: Checkbox,
    ListPage,
    Field
  },
  data () {
    let today = new Date()
    return {
      firstName: '',
      firstNameError: '',
      lastName: '',
      lastNameError: '',
      email: '',
      emailError: '',
      password: '',
      passwordError: '',
      rpassword: '',
      rpasswordError: '',
      birth: null,
      minDate: new Date(today.getFullYear() - 100, 0, 1),
      maxDate: today,
      birthError: '',
      showBirth: false,
      showBirthPicker: false,
      country: null,
      columns: null,
      showCountry: false,
      showCountryPicker: false,
      countryError: '',
      checked: false,
      checked2: false
    }
  },
  computed: {
    ...mapState(['countryList']),
    disable () {
      return !this.firstName || !this.lastName || !this.email || !this.password || !this.rpassword
    },
    birthDay () {
      if (this.birth) {
        return dateFormat(this.birth, 'MM-dd-yyyy')
      }
      return ''
    },
    currentDate () {
      return this.birth || new Date()
    },
    country2city () {
      if (this.country && this.countryList.length > 0) {
        let country = this.countryList[this.country[0]]
        if (country && country.children) {
          let province = country.children[this.country[1]]
          return `${ country.name } ${ province ? province.name : '' }`
        }
      }
      return ''
    }
  },
  methods: {
    onClickBirth () {
      this.showBirthPicker = true
      this.$nextTick(() => this.showBirth = true)
    },
    onHideBirth () {
      this.showBirthPicker = false
    },
    onConfirmBirth (date) {
      console.log('onConfirmBirth', date)
      this.birthError = ''
      this.birth = date
      this.showBirth = false
    },
    getProvinceList (index, picker) {
      if (!this.countryList[index].children) {
        this.$http.post('/api/common/area/list', {
          country: this.countryList[index].id
        }).then(data => {
          this.countryList[index].children = data.provinceList
          if (this.$country && index === this.$country[0]) {
            if (picker) {
              if (picker.getColumnValues(1).length === 0) {
                picker.setColumnValues(1, this.countryList[index].children.map(province => province.name))
              }
            } else {
              if (this.columns[1].values.length === 0) {
                this.columns[1].values = data.provinceList.map(province => province.name)
              }
            }
          }
        })
      }
    },
    onClickCountry () {
      if (this.countryList.length > 0) {
        this.$country = this.country ? [...this.country] : [0, 0]
        this.columns = [
          {
            values: this.countryList.map(country => country.name),
            defaultIndex: this.$country[0]
          },
          {
            values: (this.countryList[this.$country[0]].children || []).map(province => province.name),
            defaultIndex: this.$country[1]
          }
        ]
        this.getProvinceList(this.$country[0])
        this.showCountryPicker = true
        this.$nextTick(() => this.showCountry = true)
      }
    },
    onHideCountry () {
      this.showCountryPicker = false
    },
    onChangeCountry (picker, values, index) {
      this.$country[index] = picker.getColumnIndex(index)
      if (index === 0) {
        picker.setColumnValues(1, (this.countryList[this.$country[index]].children || []).map(province => province.name))
        this.getProvinceList(this.$country[index], picker)
      }
    },
    onConfirmCountry (values, indexs) {
      this.$country = null
      if (values[1] !== void 0) {
        this.country = indexs
        this.countryError = ''
      }
      this.showCountry = false
    },
    toSignUp () {
      let fResult = checkName(this.firstName)
      this.firstNameError = fResult.msg

      let lResult = checkName(this.lastName)
      this.lastNameError = lResult.msg

      let eResult = checkEmail(this.email)
      this.emailError = eResult.msg

      let nResult = checkPassword(this.password)
      this.passwordError = nResult.msg

      let rResult = checkPassword(this.rpassword)
      this.rpasswordError = rResult.msg

      if (fResult.pass && lResult.pass && eResult.pass && nResult.pass && rResult.pass) {
        if (this.password !== this.rpassword) {
          this.rpasswordError = this.$t('patt.pwd.diff')
          return
        }

        if (!this.checked) {
          this.$toast(this.$t('sign.signup.agreeTip'))
          return
        }

        this.$toast('sign up')
      }
    }
  }
}
</script>

<style lang="scss" scoped>
</style>
