import { Dialog } from 'vant'
import i18n from '@/i18n/index.js'
import store from '@/store/index.js'
import router from '@/router/index.js'
import http from './http.js'
import EventBus from './event-bus.js'
import JsNativeApi from './js-native-api.js'

export default {
  back () {
    EventBus.$emit('back')
  },
  home,
  explore () {
    router.replace({
      name: 'map'
    })
  },
  mine () {
    router.replace({
      name: 'account'
    })
  },
  escheduler (redirect = false) {
    if (redirect) {
      router.replace({
        name: 'escheduler'
      })
    } else {
      router.push({
        name: 'escheduler'
      })
    }
  },
  entity (id) {
    if (id) {
      router.push({
        name: 'entity',
        params: {
          id
        }
      })
    }
  },
  web (url) {
    if (url) {
      router.push({
        name: 'web',
        query: {
          url: encodeURIComponent(url)
        }
      })
    }
  },
  ticket (id) {
    if (id) {
      router.push({
        name: 'ticket',
        params: {
          id
        }
      })
    }
  },
  scan,
  alert,
  confirm: vconfirm,
  sign,
  to,
  verify,
  getStatus,
  toBuyTicket,
  linkTo,
  readMail,
  phone (phoneNumber) {
    JsNativeApi.ringUp(phoneNumber)
  }
}

function home () {
  router.replace({
    name: 'home'
  })
}

