function alert (options = {}) {
  if (typeof options === 'string') {
    options = { message: options }
  }
  let { title = i18n.t('modal.title'), message, btn = i18n.t('btn.ok') } = options
  return Dialog.alert({
    className: 'c-dialog',
    title: title === false ? false : title,
    message,
    confirmButtonText: btn
  })
}

function vconfirm (options = {}) {
  if (typeof options === 'string') {
    options = { message: options }
  }
  let { title = false, message, btn = i18n.t('btn.confirm'), cbtn = i18n.t('btn.cancel') } = options
  return Dialog.confirm({
    className: 'c-dialog',
    title: title === false ? false : title,
    message,
    confirmButtonText: btn,
    cancelButtonText: cbtn
  })
}

function sign () {
  router.push({
    name: 'sign'
  })
}

function to (path) {
  switch (true) {
    case store.getters.isAbnormal:
    case store.getters.isRegistered:
      console.log('auth failed', path)
      router.push({
        name: 'sign',
        query: {
          path
        }
      })
      break
    case store.getters.isLogined:
      path && router.push({
        name: 'sign',
        query: {
          path
        }
      })
      break
    default:
      console.log('Unable to determine status', path)
      break
  }
}

function verify (path) {
  if (path) {
    switch (true) {
      case store.getters.isAbnormal:
      case store.getters.isRegistered:
        console.log('Validation failed', path)
        router.replace({
          name: 'sign',
          query: {
            path
          }
        })
        return false
      case store.getters.isLogined:
        console.log('Verification passed', path)
        return true
      default:
        console.log('Validation failed, unable to determine status', path)
        home()
        return false
    }
  } else {
    to()
    return true
  }
}

function checkIsOpeningNow (entity) {
  let hasPFlow = entity.pflowStatus === 1
  let status = hasPFlow ? entity.pflowInfo.entityStatus : entity.openStatus
  if (status === 'open' || !status) {
    // 表演分类和未关联景点默认全天
    if (entity.typeId === 4 || !hasPFlow) {
      return true
    }
    if (hasPFlow && entity.pflowInfo.operatingHourList.length > 0) {
      let time = entity.pflowInfo.operatingHourList[0]
      let now = Date.now()
      return time.openTime <= now && time.closeTime > now
    }
  }
  return false
}

function checkIsSuspend (entity, isOpeningNow) {
  let hasPFlow = entity.pflowStatus === 1
  let status = hasPFlow ? entity.pflowInfo.entityStatus : entity.openStatus
  if (status === 'suspend') {
    return true
  }
  // 仅在营业中时判断
  if (isOpeningNow && entity.suspendTimeList && entity.suspendTimeList.length > 0) {
    let now = Date.now()
    return entity.suspendTimeList.findIndex(item => item.startTime <= now && item.endTime > now) > -1
  }
  return false
}

function checkIsClosed (entity) {
  let hasPFlow = entity.pflowStatus === 1
  let status = hasPFlow ? entity.pflowInfo.entityStatus : entity.openStatus
  if (status === 'close') {
    return true
  }
  // 有表演属性时判断表演属性
  let hasShows = entity.activityList !== void 0
  if (hasShows) {
    return entity.activityList.length === 0 || entity.activityList[0].timeList.length === 0
  }
  // 表演分类判断表演属性
  if (entity.typeId === 4) {
    return !hasShows
  }
  return hasPFlow && entity.pflowInfo.operatingHourList.length === 0
}

function getStatus (entity) {
  let isOpeningNow = false
  let isSuspend = false
  let isClosed = false
  if (entity) {
    isOpeningNow = checkIsOpeningNow(entity)
    isSuspend = checkIsSuspend(entity, isOpeningNow)
    isClosed = checkIsClosed(entity)
  }
  return {
    isOpeningNow, isSuspend, isClosed
  }
}

function toBuyTicket (type = 0) {
  to(`/pages/ticket/buy/buy?type=${ type }`)
}

function scan () {

}

function linkTo (link) {
  console.log('link', link)
  if (!link) {
    return
  }

  switch (link.linkType) {
    case 1:
      router.push({
        path: link.link
      })
      break
    case 2:
      // miniprogram
      break
    case 3:
      router.push({
        name: 'web',
        query: {
          url: encodeURIComponent(link.link)
        }
      })
      break
    case 4:
      if (link.link === 'scanQR') {
        scan()
      }
      break
    default:
      break
  }
}

function readMail (mail, prevent = false) {
  if (mail.isFresh) {
    mail.isFresh = false
    http.post('/api/common/user/msg/read', {
      msgId: mail.id,
      msgType: mail.type
    }, {
      login: true
    })
  }

  if (!prevent && mail.type === 2 && mail.origin.msgType !== 0) {
    router.push({
      name: 'mail',
      params: {
        id: mail.id
      }
    })
  }
}
